/* Variables */
:root {
  /* Primary Colors */
  --color-primary-dark100: #253000;
  --color-primary-dark90: #324000;
  --color-primary-dark80: #485C00;
  --color-primary-dark70: #729100;
  --color-primary-light100: #C8FF00;
  --color-primary-light50: #E3FF7B;

  /* Grey Shades */
  --color-grey-100: #030404;
  --color-grey-90: #101010;
  --color-grey-85: #101515;
  --color-grey-80: #1B1C19;
  --color-grey-70: #31322F;
  --color-grey-60: #3B4853;
  --color-grey-50: #727E88;
  --color-grey-30: #A3B1BC;
  --color-grey-10: #E8ECF2;
  --color-grey-0: #FFFFFF;

  /* System Colors */
  --color-system-error: #CD3E13;
  --color-system-blue: #09A0C1;
  --color-system-green: #008625;
}
/* Fonts */

/* -------------- Common Normalize Styles -------------- */

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "IBM Plex Sans Condensed", Arial, sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size:20px;
  line-height: 1.5;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  color: #E8ECF2;
  background-color: #101515;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
  margin: 0;
}

img, picture, video, iframe {
  max-width: 100%;
  display: block;
  height: auto;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:focus, a:hover {
  text-decoration: underline;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font: inherit;
}

input, textarea, select, button {
  font: inherit;
  color: inherit;
}

textarea {
  resize: vertical;
}

select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  /* background-color: #000;
  color: #fff;
  border: 1px solid #444; */
}
